import { createContext, useEffect, useState } from "react";
import $ from 'jquery';

export const AuthContext = createContext(),
// Authentication context
AuthContextProvider = (props) => {
  const [auth, setAuth] = useState(false);
  const [authError, setError] = useState({ error: false , msg: null})
  const [wsObject, setWsObject] = useState()

  useEffect(()=> {
    const token = localStorage.getItem("token")
    if(token){
      initSocket()
      setAuth(true)
    }
  },[])

  const initSocket = () => {
    if (!(window.WebSocket)) {
      alert('Your browser does not support WebSocket')
      return
    }
    const token = localStorage.getItem("token")
    const env = process.env;
    let ws = new WebSocket(`${env.REACT_APP_API_SOCKET_URL}${env.REACT_APP_API_INIT_WEBSOCKET}?token=`+token)
    setWsObject(ws)
    ws.onopen = function() {
      console.log("open")
    }
  
    ws.onmessage = function (event) {
      let res = JSON.parse(event.data)
    }
  
    ws.onclose = function() {
      console.log("close")
    }
  }
  
  // Sign in method
  const signIn = (email, password) => {
    const env = process.env;
    const settings = {
        "url": `${env.REACT_APP_API_BASE_URL}${env.REACT_APP_API_SIGNIN_URL}`,
        "method": "POST",
        "timeout": 0,
        "headers": {
          "Content-Type": "application/json",
          "X-RapidAPI-Key": `${env.REACT_APP_X_RAPID_API_KEY}`,
          "X-RapidAPI-Host": `${env.REACT_APP_X_RAPID_API_HOST}`,
        },
        "data": JSON.stringify({
          "email": `${email}`,
          "password": `${password}`
        }),
    };
      
    $.ajax(settings).always(function (response) {
        if(response ) {
            localStorage.setItem("token", response.data.token)
            localStorage.setItem("id", response.data.id)
            setAuth(true)
            initSocket()
        }
    });
  }
  // Sign out method
  const signOut = () => {
    setAuth(false)
    localStorage.removeItem("token");
    localStorage.removeItem("id");
    wsObject.close()
  }
  

  return (
    <AuthContext.Provider value={{auth, signIn, signOut, authError, wsObject}}>
      {props.children}
    </AuthContext.Provider>
  )
}

export default AuthContextProvider;
